//Primary_Theme

  $primary:    #FF6418;
  $secondary:  #3E3E3E;
  $danger:     #FF0000;
  $light:      #FDEBDF;
  $dark:       #3E3E3E;
  $white:     #fff;

$colorTop: #ff8447;
$colorBottom: #ff6e25;

