/** responsive break points **/
/*
$grid-max-widths: (
 //lg:992px
); */
ion-header, ion-tab-bar, ion-list, ion-content,
ion-toolbar {
  margin: auto; }

@media (min-width: 1200px) {
  ion-header, ion-tab-bar, ion-list, ion-list, ion-toolbar {
    margin: 0;
    padding-right: calc((100% - 1200px) / 2);
    padding-left: calc((100% - 1200px) / 2); }
  ion-tab-bar {
    position: absolute;
    margin: auto;
    width: -webkit-fill-available; } }

@media only screen and (min-width: 320px) {
  ion-tab-bar {
    margin: 0;
    padding-right: calc((100% - 320px) / 3);
    padding-left: calc((100% - 320px) / 3); } }

