/*Modified Styles*/
@import  "../../../theme/variables.scss";
@import  "../../../theme/responsive-styles.scss";

ion-toolbar {
  ion-title {
    color: #fff;
  }
}

ion-header {
  background-color: #F6F6F6;
  height: 150px;
  @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
    height: 200px;
    padding-top: 70px;
  }
  
  .header_toggle_div {
    width: 239px;
    margin: auto;
  }
  transition: top 0.6s;	
}	
.hide-header{	
  top: -50px;
}

#short-div{
  // size: 25%;
  // padding-right: 0;
  margin-top: 13px;
  padding-left: 15px;
  ion-col{
    padding: 0;
  }
  .rec_text{
    font-size: 12px;
    opacity: 0.7;
    padding-top:3px;
  }
  ion-badge{
    margin-left: 5px;
    height: 22px;
    width: 122px;
    text-align: left;
    background-color: #FEE7D8;
    border: 1px solid var(--ion-color-primary);
    border-radius: 20px;
    color: var(--ion-color-primary);;
    float: left;
    ion-label {
      font-size: 11px;
    }
    .badge_sortby_select{
      color: var(--ion-color-primary);;
      font-size: 11px;
      margin-top: -7px;
      float: left;
      padding-left: 8px;

      .select-placeholder{
        opacity: 0.8
      }
    }
      
    
  }
  .badge_sortby_select .select-icon-inner{
    margin-top: -7px;
  }
  .header_badge_select{
    margin-left: 12px;
    width: 85px;
    ion-select{
      top: -8px;
      float: left;
      padding: 9px;
  
    }
   
  }

}
.card_filter_badge {
    height: 24px;
    background-color: #F6D6791A;
    color: #696565;
    border: 1px solid #FF64184D;
    font-size: 11px;
    padding: 6px;
    font-family: Helvetica Neue, regular;
    border-radius: 12px;
    margin-right: 2px;
}

#header-title{
  margin-bottom:10px;
  font-size:22px;
  color: #FF6418;
  margin-left:13px;
  margin-top:20px;
}

.card_arrow_img {
  height: 14px;
  width: 24px !important;
  margin-top: 3px;
}
.card-content{
  padding: 10px;
}

.card-col{
  size: 5.5px;
}

.card-name{
  font-size:14px;
  color: var(--ion-color-primary);
  opacity: 0.8;
}

.card-dt{
 font-size:11px;
 color:#4A4A4A;
}

.card-dist{
 font-size:11px;
 color: darkgray;
}

.card-icon{
  font-size:30px;
  margin-top:13px;
}

.right{
  text-align: right;
  padding-right: 0;
}

.card_price{
  text-align: right;
  font-size: 18px;
  font-weight: bold;
}
.sn-toggle{
  .sn-layer{
      position: relative;
      top: 50%;
      width: 100%;
      height: 38px;
      overflow: hidden;
      background-color: #DDDEDFA1;
      z-index: 1;
     border-radius: 100px;
  }
  button {
      background: none;
      margin-left:4px;
      font-size: 14px;
      line-height: 2;
      border:none;
      min-width: 64px;
      margin-top:4px;
      padding-left: 18px;
      text-align: left;
  }

  button:last-child{
      margin-right: 5px;
      float: right;
      padding-right: 12px;
  }
  
  button:active, button:focus{
      border-radius: 20px;
      background-color: #fff;
      outline: none; 
  }
}
.new-search{
  width: 325px;
}
.recommended{
  width: 239px;
 
}

ion-item-divider{
  min-height: 1px!important;
}
.recommended-Error{
  font-size: 18px;
    padding: 0px;
}
.recommended-Error-img{
  opacity: 100%;
}
.No-Recommended{
  text-align: center;
  padding: 22px;
}
.RecommendedSearch{
  text-transform: inherit;
  font-size: 21px;
  margin: auto;

  width: 298px;
  height: 55px;
}
.SearchLoadBtn{
    padding-top: 46px;
}
.load-search{
  font-size:2.0rem;
}
.scn-card{
  margin: 0px 1px 10px 1px !important;
  border:0.5px solid #DEDEDE;
  box-shadow: 0px 3px 6px #3A4C8212;
  border-radius:5px;
}
.loadCardFooter{
  padding-top:6px;
}
.loadCardArrow{
  text-align: -webkit-center;
  padding-top: 19px;
}